@media screen and (max-width: 550px) {
    header {
        width: 100%;
        position: static;
        height: 248px;
        opacity: 1;
    }

    footer {
        width: 100%;
        height: 100px;
        position: relative;
    }

    body {
        width: 100%;
    }

    img {
        max-width: 100%;
    }

    .headerContent {
        overflow: visible;
        height: 100%;
        width: 100%;
        border: 1px solid black;
        padding: 0;
    }

    .footerItem {
        display: block;
    }

    .headerItem {
        display: block;
        width: 100%;
        margin: 1px 0;
        border: 1px solid black;
        height: 16%;

    }

    .txt01, .txt02, .txt03, .txt04 {
        padding-left: 1.5rem;
        padding-right: 1.5rem;
    }

    .imgRobo{
        position: relative;
        bottom: 0;
        right: 0;
    }


    [class*="col-"] {
        width: 100%;
    }
}